import { TypeAnimation } from "react-type-animation";
function  Load () {
    return (
        <div
          className="overflow-hidden light:bg-white bg-gray-900 pt-[58%] content-center sm:pt-[20%]"
          style={{ height: "100vh" }}
        >
          <div style={{ margin: "auto" }}>
            <img
              className="w-36	 m-auto"
              src="https://www.bluefrogrobotics.com/wp-content/resources/BUDDY3D/Ressources/img/emoji/ClinDOeil_Big.gif"
            ></img>
            <h1 style={{ textAlign: "center" }}>
              <TypeAnimation
                className="text-xl font-sasn font-semibold mt-2 text-gray-300 sm:text-2xl"
                sequence={[
                  "Javascript", // Types 'One'
                  300, // Waits 1s
                  "React", // Deletes 'One' and types 'Two'
                  700, // Waits 2s
                  "Vue",
                  1200,
                  "Vue", // Types 'Three' without deleting 'Two'
                ]}
                wrapper="span"
                cursor={false}
                repeat={Infinity}
              />
            </h1>
          </div>
        </div>
      );
}

export default Load